import React                                        from 'react';
import PropTypes                                    from 'prop-types';
import {
    connect
}                                                   from 'react-redux';



import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow,
    TableRowColumn,
}                                                   from 'material-ui/Table';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import TextField                                    from 'material-ui/TextField';
import RaisedButton                                 from 'material-ui/RaisedButton';

import FlatPagination                               from 'material-ui-flat-pagination';







/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionCompanyOnBoardApprove , { ReduxEnumCompanyOnBoardApprove } from '../../../../_redux/action/reduxAction.company.onboard.approve';








/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../../_helper/h.navigate';
import HelperStorage		                        from '../../../_helper/h.storage';
import HelperVerifyAuthorize                        from '../../../_helper/h.verifyAuthorize';

import { ObjectUtil }                            	from '../../../../_util/util.object';







/* ################################################### */
/*          component         */
/* ################################################### */
import ComponentAbstract                            from '../../../_abstract/c.abstract';


import ComponentCustomAlert                         from '../../../_component/alert/cc.alert';
import ComponentCustomLoading                       from '../../../_component/loading/cc.loading';





import './s.onboardApprove.list.css';









export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxCompanyOnboardApprove' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumCompanyOnBoardApprove.INIT_DONE == recordItem[ 'type' ] ) {

                        } else if ( ReduxEnumCompanyOnBoardApprove.LIST_TASK_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestTaskDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumCompanyOnBoardApprove.ERROR == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestError';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

         class ComponentCompanyOnboardApproveList extends ComponentAbstract {



            constructor( props ) {
                super( props )

                this.state = {
                    isLoading               : false

                    , isError               : false
                    , errorMsg              : ''

                    , result                : []
                }
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestTask();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */


            onClickDetail( instructionId ) {
                console.log( 'ComponentCompanyOnboardApproveList : onClickDetail : *** : instructionId => ' , instructionId );

                let record = {};
                for ( let i = 0; i < this.state.result.length; i ++ ) {
                    let tmpRecord = this.state.result[ i ];
                    if ( tmpRecord.instructionId == instructionId ) {
                        record = tmpRecord;
                    }
                }

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.COMPANY_ONBOARD_APPROVE_DETAIL )
                    , record
                );

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestTask() {
                console.log( 'ComponentCompanyOnboardApproveList : requestTask : *** ' );


                this.setState(
                    { isLoading : true }
                    , () => {

                        HelperVerifyAuthorize.verify()
                            .then(
                                () => {

                                    Promise.all( [
                                        ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                        , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                    ] ).then(
                                        ( value ) => {
                                            this.dispatch(
                                                ReduxActionCompanyOnBoardApprove.rListTask(
                                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                    , { }
                                                )
                                            )
                                        }
                                    )

                                }
                            ).catch (
                                ( error ) => {
                                    this.requestError( error );
                                }
                            ) ;


                    }
                );

            }
            requestTaskDone( result ) {
                console.log( 'ComponentCompanyOnboardApproveList : requestTaskDone : *** : result => ' , result );

                this.setState( {
                    isLoading : false
                    , result : result
                } );
                this.dispatch( ReduxActionCompanyOnBoardApprove.rRequestEnd( ) );

            }


            requestError( result ) {
                console.log( 'ComponentCompanyOnboardApproveList : requestError : *** : result => ' , result );

                this.setState(
                    { isLoading : false }
                    , () => {

                        this.handlerException(
                            result
                            , () => {
                                this.setState( {
                                    isError         : true
                                    , errorMsg      : result.msg
                                } );
                            }
                        );

                    }
                )

                this.dispatch(
                    ReduxActionCompanyOnBoardApprove.rRequestEnd( )
                );
            }
















            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */
            render() {

                return (

                    <div>

                        <Paper >
                            <Table
                                fixedHeader={ false }
                                style={ { flexGrow: '100%', tableLayout: "auto" } }>
                                <TableHeader
                                        adjustForCheckbox={ false }
                                        displaySelectAll={ false }>
                                    <TableRow>
                                        <TableHeaderColumn> CIF Number </TableHeaderColumn>
                                        <TableHeaderColumn> Service Package </TableHeaderColumn>
                                        <TableHeaderColumn> Company Name </TableHeaderColumn>
                                        <TableHeaderColumn> Action </TableHeaderColumn>
                                    </TableRow>
                                </TableHeader>


                                <TableBody displayRowCheckbox={ false } showRowHover={ true }>
                                        {
                                            this.state.result.map( ( item , i ) => {

												if ( !ObjectUtil.hasProperty( item , 'instructionObject' ) ) {
													return ( null );
												}

                                                return (

                                                    <TableRow
                                                                    key={ i }>

                                                        <TableRowColumn style={ { width: 300 } }>{ item.instructionObject.companyOnboardingInst.cifNumber }</TableRowColumn>
                                                        <TableRowColumn style={ { width: 300 } }>{ item.instructionObject.companyOnboardingInst.spCode }</TableRowColumn>

                                                        <TableRowColumn>{ item.instructionObject.companyProfile.bussName }</TableRowColumn>

                                                        <TableRowColumn style={ { width: 300 } } >

                                                            <FlatButton
                                                                    label="OnBoard"
                                                                    secondary={ true }
                                                                    onClick={ () => {
                                                                        this.onClickDetail( item.instructionId )
                                                                    }}
                                                            />

                                                        </TableRowColumn>

                                                    </TableRow>
                                                )

                                            } )
                                        }
                                </TableBody>
                            </Table>
                        </Paper>


                        <ComponentCustomLoading
                            visible={ this.state.isLoading }
                        />


                        <ComponentCustomAlert
                            visible={ this.state.isError }

                            title={ 'Error' }
                            msg={ this.state.errorMsg }

                            onClose={ () => {
                                this.setState( {  isError : false } );
                            } }

                            alertType={ 'error' }
                        />

                    </div>

                );
            };
        }

    )



